.demands {
  margin: 30px 150px;
}

.demands-container {
  background-color: #ededed;
  margin-top: 30px;
}

.demands-conatiner-text {
  padding: 20px;
  background-color: white;
  text-transform: uppercase;
  color: #142239;
  margin: 80px 0 50px 0;
  -webkit-box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.demands-row {
  display: flex;
  width: 100%;
  margin-bottom: 60px;
}

.demands-box {
  height: 68vh;
  width: 30.3%;
  background-color: white;
  -webkit-box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.demands-box-1 {
  margin-right: 50px;
}

.demands-box-2 {
  margin-right: 50px;
}

.demands-box-4 {
  margin-right: 50px;
}

.demands-box-5 {
  margin-right: 50px;
}
.demands-img-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 190px;
  width: 100%;
  overflow: hidden;
}
.demands-img-container:hover {
  cursor: pointer;
}

.demands-img {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.7;
}
.fa-play-circle {
  z-index: 300;
  text-align: center;
  font-size: 140px;
  opacity: 0.4;

  color: black;
}

a {
  text-decoration: none;
}
.demands-title {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 18px;
  padding: 20px;
  color: #142239;
  font-weight: 600;
  line-height: 25px;
}
.demands-description {
  padding: 0 30px;
  text-align: justify;
  line-height: 20px;
  font-size: 15px;
  color: #142239;
}

.button-container {
  font-size: 14px;
  text-align: center;
}

.button-text-box {
  text-align: center;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: darkblue;
  color: white;
  margin-top: 15px;
  border-radius: 4px;
  padding: 9px 15px;
  display: inline-block;
  transition: 0.3s;
}

.button-text-box:hover {
  cursor: pointer;
  opacity: 0.8;
}
