.poll {
  height: 70vh;
  overflow: hidden;
  background: url(../images/suits.jpg);
  width: 100%;
  background-size: cover;
  z-index: -1;
  position: relative;
}
.poll-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(56, 56, 187, 0.85),
    rgba(15, 15, 75, 0.85)
  );
  color: white;
}

.poll-header {
  margin: 70px 0 60px 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 25px;
  text-shadow: 2px 2px black;
}
.poll-question-box {
  display: inline-block;
}
.poll-question {
  background-color: white;
  color: darkblue;
  padding: 20px 40px;
  border-radius: 15px;
}
.poll-answers-box {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  padding: 20px 40px;
}

.poll-answer-yes {
  background-color: green;
  padding: 20px 45px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.poll-answer-no {
  background-color: red;
  padding: 20px 45px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.poll-answer-yes:hover {
  background-color: lightcoral;
  cursor: pointer;
}

.poll-answer-no:hover {
  background-color: lightgreen;
  cursor: pointer;
}
